<!--  -->
<template>
    <div class="sign-wrapper">
        <div class="sign-main sign-main-register">
            <!-- 左侧form -->
            <div class="sign-main_left">
                <h1>{{$t('sign.register[0]')}}</h1>
                <h4>{{$t('sign.register[1]')}}</h4>
                <el-form :model="form" status-icon :rules="rules" label-position='top' ref="form" label-width="80px"
                    class="form-register form">
                    <!-- 手机号/邮箱 -->
                    <el-form-item :label="$t('sign.register[2]')" prop="mobile">
                        <el-input :placeholder="$t('sign.register[3]')" type="text" v-model="form.mobile" autocomplete="off">
                        </el-input>
                    </el-form-item>
                    <!-- 验证码 -->
                    <el-form-item :label="$t('sign.register[4]')" prop="code">
                        <div class="verifyCode">
                            <el-input :placeholder="$t('sign.register[5]')" type="text" v-model="form.code" autocomplete="off">
                            </el-input>
                            <el-button :loading="codeLoading" :disabled="btnCode.disabled" @click="getCode">
                                {{btnCode.time}}
                            </el-button>
                        </div>
                    </el-form-item>
                    <!-- 登录密码 -->
                    <el-form-item :label="$t('sign.register[6]')" prop="opwd">
                        <div style="position: relative">
                            <el-input :placeholder="$t('sign.register[7]')" :type="lookMyBaby?'text' :'password'" v-model="form.opwd"
                                autocomplete="off"></el-input>
                            <!-- 小眼睛 -->
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="!lookMyBaby"
                                src="../../assets/img/sign/login_icon_7.png" alt="">
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="lookMyBaby"
                                src="../../assets/img/sign/login_icon_9.png" alt="">
                        </div>
                    </el-form-item>
                    <!-- 确认密码 -->
                    <el-form-item :label="$t('sign.register[8]')" prop="opwd1">
                        <div style="position: relative">
                            <el-input :placeholder="$t('sign.register[9]')" :type="lookMyBaby?'text' :'password'"
                                v-model="form.opwd1" autocomplete="off"></el-input>
                            <!-- 小眼睛 -->
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="!lookMyBaby"
                                src="../../assets/img/sign/login_icon_7.png" alt="">
                            <img class="lookMyBaby" @click="lookMyBaby = !lookMyBaby" v-show="lookMyBaby"
                                src="../../assets/img/sign/login_icon_9.png" alt="">
                        </div>
                    </el-form-item>
                    <!-- 邀请码 -->
                    <el-form-item :label="$t('sign.register[10]')" prop="tjuser">
                        <div>
                            <el-input :placeholder="$t('sign.register[11]')" type="text" v-model="form.tjuser" autocomplete="off">
                            </el-input>
                        </div>
                    </el-form-item>
                    <el-form-item>
                            <!-- 我已阅读并同意 -->
                        <el-checkbox v-model="isChecked">{{$t('sign.register[12]')}}</el-checkbox><span @click="checkedRedBook"
                            class="redBook">{{$t('sign.register[13]')}}</span>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" :loading="loading" class="btn-submit" @click="submitForm('form')">{{$t('sign.register[14]')}}
                        </el-button>
                        <!-- 已有账号？ -->
                        <span class="guide">{{$t('sign.register[15]')}}<a @click="$router.push({path:'/login'})">{{$t('sign.register[16]')}}</a></span>
                    </el-form-item>
                </el-form>
            </div>
            <!-- 分割线 -->
            <div class="river"></div>
            <!-- 右侧图片 -->
            <div class="sign-main_right sign-main_right-register">
                <img src="../../assets/img/sign/login_img_2.png" alt="">
                <p>{{$t('sign.register[17]')}} </p>
            </div>
        </div>
        <!-- 用户协议 -->
        <el-dialog :title="$t('sign.register[18]')" :visible.sync="dialog_agree" width="500">
            <div v-html="agreeContent">
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="looked">{{$t('sign.register[19]')}}</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
import { register, sendMsg, sendEmailMsg, agree } from "@/api/public";
export default {
  data() {
    var vAccount = (rule, value, callback) => {
      // 请输入手机号/邮箱地址
      if (!value) {
        callback(new Error(this.$t("sign.tips[0]")));
        this.btnCode.disabled = true;
      }
      if (!this.$public.email(value) && !this.$public.checkMobile(value)) {
        callback(new Error(this.$t("sign.tips[1]")));
        this.btnCode.disabled = true;
      } else {
        this.btnCode.disabled = false;
        callback();
      }
    };
    var vPwd = (rule, value, callback) => {
      if (!this.$public.pwd(value)) {
        callback(new Error(this.$t("sign.tips[2]")));
      } else {
        callback();
      }
    };
    var vPwd1 = (rule, value, callback) => {
      if (!this.$public.pwd(value)) {
        callback(new Error(this.$t("sign.tips[2]")));
      }
      if (this.form.opwd != value) {
        callback(new Error(this.$t("sign.tips[3]")));
      } else {
        callback();
      }
    };

    return {
      btnCode: {
        // 获取验证码
        time: this.$t("sign.tips[4]"),
        disabled: false
      },
      dialog_agree: false,
      setTimer: "",
      lookMyBaby: false,
      isChecked: true,
      loading: false,
      codeLoading: false,
      agreeContent: "",
      form: {
        mobile: "",
        code: "",
        mcode: "86",
        opwd: "",
        opwd1: "",
        tjuser: ""
      },
      // 验证规则
      rules: {
        mobile: [{ validator: vAccount, trigger: "blur" }],
        // 请输入验证码
        code: [
          { required: true, trigger: "blur", message: this.$t("sign.tips[5]") }
        ],
        // 密码不能为空
        opwd: [
          { validator: vPwd, trigger: "blur" },
          { required: true, trigger: "blur", message: this.$t("sign.tips[7]") }
        ],
        opwd1: [
          { validator: vPwd1, trigger: "blur" },
          { required: true, trigger: "blur", message: this.$t("sign.tips[7]") }
        ],
        // 请输入邀请码
        tjuser: [
          { required: true, trigger: "blur", message: this.$t("sign.tips[6]") }
        ]
      }
    };
  },

  components: {},

  computed: {},

  methods: {
    submitForm(form) {
      this.$refs[form].validate(v => {
        if (v) {
          if (!this.isChecked) {
            // 请先同意用户协议
            this.$public.msg(this.$t("sign.tips[8]"), "warning", this);
            return;
          }
          this.loading = true;
          var sendData = JSON.parse(JSON.stringify(this.form));
          //   sendData.opwd = this.$public.CryptographicLand(sendData.opwd);
          //   sendData.opwd1 = this.$public.CryptographicLand(sendData.opwd1);

          sendData.opwd = sendData.opwd;
          sendData.opwd1 = sendData.opwd1;

          register(sendData)
            .then(r => {
              this.loading = false;
              if (r.data.status == 200) {
                // 注册成功！
                this.$public.msg(this.$t("sign.tips[9]"), "success", this);
                this.$router.push({ path: "/login" });
              } else {
                this.$public.msg(r.data.msg, "error", this);
              }
            })
            .catch(r => {
              this.loading = false;
            });
        }
      });
    },
    // 获取验证码
    getCode() {
      if (!this.form.mobile) {
        // 账号不能为空
        this.$public.msg(this.$t("sign.tips[10]"), "warning", this);
        return;
      }
      this.codeLoading = true;
      if (this.form.mobile.indexOf("@") > -1) {
        // 邮箱
        sendEmailMsg({ email: this.form.mobile }).then(r => {
          this.codeLoading = false;
          if (r.data.status == 200) {
            this.successMsg();
          } else {
            this.$public.msg(r.data.msg, "error", this);
          }
        });
      } else {
        // 手机
        sendMsg({ mcode: this.form.mcode, mobile: this.form.mobile, type: 1 })
          .then(r => {
            this.codeLoading = false;
            if (r.data.status == 200) {
              this.successMsg();
            } else {
              this.$public.msg(r.data.msg, "error", this);
            }
          })
          .catch(r => {
            this.codeLoading = false;
          });
      }
    },

    successMsg() {
      this.btnCode.time = 59;
      this.btnCode.disabled = true;
      this.$message({
        // 验证码发送成功
        message: this.$t("sign.tips[11]"),
        type: "success"
      });
      this.setTimer = setInterval(() => {
        this.btnCode.time--;
        if (this.btnCode.time <= 0) {
          this.btnCode = {
            // 重获验证码
            time: this.$t("sign.tips[12]"),
            disabled: false
          };
          clearInterval(this.setTimer);
        }
      }, 1000);
    },
    // 查看协议
    checkedRedBook() {
      agree({ type: "reg_agree" })
        .then(r => {
          if (r.data.status == "200") {
            if (r.data.status == 200) {
              this.dialog_agree = true;
              this.agreeContent = r.data.data;
            }
          } else {
            this.$public.msg(r.data.msg, "warning", this);
          }
        })
        .catch(function(error) {});
    },
    looked() {
      this.dialog_agree = false;
      this.isChecked = true;
    }
  },
  beforeDestroy() {
    clearInterval(this.setTimer);
  }
};
</script>
<style lang=less>
@import "./sign.less";
@import "../../assets/index.less";
</style>


